import BaseComponent from '../../base-component';
import navigationItems from './navigation-items';
import Link from '../../link/link';
import './header-nav.scss';
export default class HeaderNav extends BaseComponent {
    constructor() {
        super('nav', ['header__nav']);
        this.node.append(this.createMarkup());
    }
    createMarkup() {
        const navigationList = new BaseComponent('ul', [
            'header__nav-list',
        ]).getNode();
        for (const navItem of navigationItems) {
            const navigationItem = this.createNavItem(navItem, ['header__nav-item']);
            navigationList.append(navigationItem);
        }
        return navigationList;
    }
    createNavItem(item, classNames) {
        const navigationItem = new BaseComponent('li', [
            ...classNames,
        ]).getNode();
        const itemLink = new Link(item.title, ['header__link', 'link--inline', 'link--inline_dark'], item.href).getNode();
        navigationItem.append(itemLink);
        return navigationItem;
    }
}
